@font-face {
  font-family: 'RobotoMono';
  src: url(/react-crud/frontend/build/static/media/Roboto-Thin.89e2666c.ttf);
}
:root {
    --bg-dark: #1A2F3A;
    --logo-height: 100px;
    --header-height: 100px;
    --aside-width: 225px;
    --footer-height: 40px;
    --shadow: 0 2px 23px 0 rgba(0,0,0,0.1),
              0 2px 49px 0 rgba(0,0,0,0.06);
    --shadow-nav:  2px 0 10px 0 rgba(0,0,0,0.12),
                   2px 0 15px 0 rgba(0,0,0,0.09);
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;  
    font-family: 'RobotoMono', monospace;
    
}

.app {
    height: 100vh;
    margin: 0;
    display: grid;
    grid-template-columns: 225px 1fr;
    grid-template-columns: var(--aside-width) 1fr;
    
    grid-template-rows: 100px
                        1fr          
                        var(--fotter-height);
    
    grid-template-rows: var(--header-height)
                        1fr          
                        var(--fotter-height);
    grid-template-areas: "logo header"
                         "menu content"
                         "footer footer";
    background-color: #f5f5f5; 
}

aside.logo {
    grid-area: logo;
}
header.header {
    grid-area: header;
}
aside.menu-area {
    grid-area: menu;
}

main.content {
    grid-area: content;
}
footer.footer {
    grid-area: footer;
}
main.content {
    height: calc(100vh - var(--header-height) - var(--footer-height));
    background-color: #f2f2f2;
}

main > section {
    background-color: #FFF;
    box-shadow: 0 0 15px #0004;
}
header.header {
    height: var(--header-height);
    background-color: #fff;
    padding: 0 15px;
    overflow: hidden;
    white-space: nowrap;
    box-shadow: var(--shadow);
}

header.header h1 {
    font-size: 1.8em;
}
aside.logo {
    height: var(--logo-height);
    background-color: var(--bg-dark);
    display: flex;
    justify-content: center;
    align-items: center;
}

aside.logo img {
    height: 50px;
    padding: 0 15px;
}
aside.menu-area {
    background-color: var(--bg-dark);
    box-shadow: var(--shadow-nav);
}

.menu a{
    display: block;
    text-decoration: none;
    color: #fff;
    font-weight: 300;
    padding: 15px;
}

.menu a:hover {
    background: linear-gradient(135deg, #07a7e3 0%, #32dac3 100%);
}
a {
    display: flex;
    justify-content: flex-start;
    align-items: baseline;
}
i {
    margin-right: 10px;
}
footer.footer {
    height: var(--footer-height);
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 0 25px;
    background-color: #fff;
    box-shadow: var(--shadow);
}
